.preview-panel{
  position:relative;
  display:none;
  width:100%;
  height:100%;
  background:#3f3f3f;

  &.show{
    display:flex;
    flex-direction:column;
  }

  .btn{
    display:flex;
    margin:10px;
    font-size:14px;

    div{
      width:100px;
      height:30px;
      background:#005df7;
      border-radius:3px;
      line-height:30px;
      text-align:center;
      color:#FFF;

      &:hover{
        background:#336dff;
      }
    }
  }

  .type{
    display:flex;
    margin:0 10px 10px;

    label{
      margin-right:10px;
    }

    input{
      margin-right:2px;
    }
  }

  .container{
    flex:1;
    display:flex;
    padding:0 10px 10px;

    .menu{
      margin:0 10px 0 0;
      width:100px;
      background:#313131;
    }

    .view{
      flex:1;
      display:flex;
      flex-direction:column;
      justify-content:space-between;
      position:relative;
      background:#313131;

      .stage{
        flex:1;
        box-sizing:border-box;
        position:relative;
        margin:10px;
      }

      .canvas{
        position:absolute;
        left:50%;
        top:50%;
        border:1px solid #444;
        background:#000;
        transform:translate3d(-50%, -50%, 0);

        &.mosaic{
          background:url(bg.png) repeat center;
          background-size:8px 8px;
        }

        canvas,svg{
          display:block;
          width:100%;
          height:100%;
        }
      }

      .control{
        display:flex;
        align-items:center;
        padding:10px;
        background:rgba(0, 0, 0, 0.5);
        color:#DDD;

        .bg{
          margin-right:5px;
          box-sizing:border-box;
          width:24px;
          height:24px;
          background:#000;
          border:2px solid #5f6061;
          border-radius:12px;

          &.mosaic{
            background:url(bg.png) repeat center;
            background-size:8px 8px;
          }
        }

        .play{
          display:none;
          width:24px;
          height:24px;
          background:url(play.png);
          background-size:cover;
          opacity:0.8;

          &.show{
            display:block;
          }

          &:hover{
            opacity:1;
          }
        }

        .pause{
          display:none;
          width:24px;
          height:24px;
          background:url(pause.png);
          background-size:cover;
          opacity:0.8;

          &.show{
            display:block;
          }

          &:hover{
            opacity:1;
          }
        }

        .time, .time2{
          margin-left:5px;
        }

        .progress{
          flex:1;
          position:relative;
          margin:0 15px;
          height:8px;
          background:#555;
          border-radius:4px;

          .bar{
            position:absolute;
            left:0;
            top:0;
            width:50%;
            height:50%;
            background:#81d4ff;
            border-radius:5px;
          }

          .drag{
            position:absolute;
            left:-10px;
            top:-4px;
            width:8px;
            height:8px;
            background:#000;
            border:4px solid #008efe;
            border-radius:8px;
            opacity:0.8;

            &:hover{
              opacity:1;
            }
          }
        }
      }

      //canvas{
      //  position:absolute;
      //  left:50%;
      //  top:50%;
      //  box-sizing:border-box;
      //  max-width:100%;
      //  max-height:100%;
      //  border:1px solid #444;
      //  background:#000;
      //  transform:translate3d(-50%, -50%, 0);
      //}
    }

    .side{
      margin:0 0 0 10px;
      padding:10px;
      width:140px;
      background:#313131;

      label{
        display:block;
        margin-bottom:5px;

        input[type=number]{
          margin-left:5px;
          padding:0 5px;
          width:24px;
          height:24px;
          background:#000;
          border:none;
          border-radius:3px;
          color:#FFF;
          text-align:center;
        }

        input[type=checkbox]{
          margin-right:2px;
        }
      }

      .btn{
        display:flex;
        justify-content:space-between;
        margin:10px 0;

        .export, .upload{
          width:65px;
          height:30px;
          background:#005df7;
          border-radius:3px;
          line-height:30px;
          text-align:center;
          color:#FFF;

          &:hover{
            background:#336dff;
          }

          &:active{
            color:#E30;
          }
        }
      }
    }
  }
}
